import React, {useRef, useState} from "react";
import {useMemo} from "react";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import WriteHttp from "../../../http/writeHttp";
import {useSelector} from "react-redux";
import styled from "@emotion/styled";
const Quill = ({quillValue,setQuillValue}) => {
    const { userId } = useSelector(state => state.persistedReducer.userReducer);
    const writeHttp = new WriteHttp();


    //    
    const imageHandler = () => {


        // 1.   input type=file DOM .
        const input = document.createElement('input');
        //  
        input.setAttribute('type', 'file');
        input.setAttribute('accept', 'image/*');
        input.click(); //     input .
        // input    .

        // input   =  
        input.addEventListener('change', async () => {

            const file = input.files[0];
            console.log(file.length);

            // multer    .
            const formData = new FormData();
            formData.append('img', file); // formData - 
            //  multer  .
            try {
                const {result} = await writeHttp.imgUpload({imageFile: file, user_id:userId });
                const IMG_URL = result.url;
                //  URL img  src          
                // src base64   URL        
                //     uploads      URL .

                //     -   .
                const editor = quillRef.current.getEditor(); //   
                // 1.  root innerHTML 
                // editor root   .  img .
                //   ->    URL  ->      .
                // editor.root.innerHTML =
                //   editor.root.innerHTML + `<img src=${IMG_URL} /><br/>`; //     .

                // 2.     
                const range = editor.getSelection();
                //    
                editor.insertEmbed(range.index, 'image', IMG_URL);
            } catch (error) {
                console.log('');
            }
        });
    };
    const modules = useMemo(() => {
        return {
            toolbar: {
                container: [
                    [{ header: [1, 2, 3, false] }],
                    ['image','link'],
                    ['bold', 'italic', 'underline', 'strike', 'blockquote'],

                ],
                handlers: {
                    image: imageHandler,
                },
            },
        };
    }, []);

    const formats = [
        'header',
        'bold',
        'italic',
        'underline',
        'strike',
        'blockquote',
        'image',
    ];
  const quillRef= useRef();




    return (
        <QuillWrap>
            <ReactQuill
                ref={quillRef}
                theme="snow"
                placeholder=" "
                value={quillValue}
                onChange={setQuillValue}
                modules={modules}
                formats={formats}
            />

        </QuillWrap>
    );
};

export  default Quill

const QuillWrap = styled.div`
  margin:20px 0;
    .ql-editor {
      min-height: 500px;
    }
`